.Card {
    position: relative;
    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;

    height: 240px;
    width: 200px;
    min-width: 200px;
    min-height: 240px;

    border: 5px solid black;
    border-radius: 8px;

    background-color: rgba(0, 0, 255, 0.4);

    overflow: hidden;
    scroll-snap-align: center;

    &>a {
        text-decoration: none;

        position: relative;
        display: flex;
        flex-direction: column;

        padding: 10px;

        width: 100%;
        height: 100%;

        justify-content: flex-end;
        align-items: flex-end;

        z-index: 1;


        .title {
            width: 100%;
            font-family: "Sour Gummy", sans-serif;
            text-align: start;
            margin-left: 4px;
            font-size: 1.1rem;

            color: rgb(34, 223, 233);

            z-index: 4;
            background-color: rgba(175, 155, 232, 0.4);
        }
    }
}

.img {
    position: absolute;
    transform: translate(-50%, -50%);
    max-width: 100%;
    max-height: 100%;

    object-fit: cover;

    filter: brightness(30%);
    transition: filter 1s, transform .6s;
    transform: scale(1.2);
}

.ico {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 2px solid blanchedalmond;
    transition: transform 2s;

    z-index: 1;
}

.Card:hover>img {
    filter: blur(1.4) brightness(50%);
    transform: scale(1);
}
.Card:hover>a>img{
    transform: scale(1.2);
}