.SpecialDiv {
    display: flex;
    flex-direction: column;

    margin: 30px 0;
    padding: 6px 10px;

    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;

    -webkit-box-shadow: 0px 13px 5px -8px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 13px 5px -8px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 13px 5px -8px rgba(0, 0, 0, 0.75);

    .title {
        display: flex;
        flex-direction: column;
        text-align: center;

        min-height: 12vh;

        .txt {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            align-items: center;
            align-self: center;

            font-size: 2.2rem;
            font-family: "Doto", sans-serif;

            &>svg {
                margin: 0 10px;
                width: 2.4rem;
                height: 2.4rem;
            }
        }
    }

    .title::after {
        content: '━━━━━━━ ◦ ❖ ◦ ━━━━━━━';

        height: 1rem;
        overflow: hidden;
    }

    .SpecialDiv_main {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        
        height: 300px;
        width: 100%;

        overflow-x: auto;
        overflow-y: hidden;
        
        gap: 20px;               /* Espacio entre las tarjetas */
    scroll-snap-type: x mandatory; /* Suaviza el desplazamiento en pantallas */
    }
}

.salto {
    margin-left: 12px;
    font-family: "Sour Gummy", sans-serif
}