.main {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;

  margin-top: 20px;
  padding-top: 20px;

  overflow-y: auto;
  overflow-x: hidden;


  scrollbar-color: cornflowerblue rgb(18, 16, 36);
  z-index: 5;

  &>main {
    height: auto;

    width: 95%;
    padding-top: 20px;
  }
}

.return {
  position: absolute;

  left: 1em;
  top: 60px;


  padding: 4px;
  border: 4px;

  color: rgb(195, 221, 243);
  background-color: rgba(38, 32, 32, 0.774);
  outline: 2px solid rgba(4, 4, 14, 0.359);

  font-size: 1.4rem;
}

.hidden {
  position: absolute;
  width: 100%;
  opacity: 0 !important;
  z-index: -1;
}

.superUl {
  opacity: .92;
  display: flex;
  flex-direction: column;
  transition: opacity .6s;

  &>li {
    position: relative;
    margin: 10px 0;

    width: 100%;
    height: 200px;
  }

  list-style: none;
}

/* ---------------------------------
Botones especiales

> Elem
elem

--------------------------------- */
.buttonsSpecials {
  display: flex;
  flex-direction: column;

  width: 100%;
  overflow: hidden;



  text-align: start;

  background-color: rgba(38, 32, 32, 0.774);
  color: rgb(195, 221, 243);

  transition: margin-top .7s, transform .2s;
  transition-delay: .8s;

  border: 2px;
  outline: 2px solid rgb(0, 0, 0);

  cursor: pointer;

  &>h1 {
    margin-left: 10px;
  }

  &>.divSpecial {    
    width: 100%;
    height: 120px;

    transition: height 2s;
    interpolate-size: allow-keywords;

    

    outline: 1px solid black;
  }
}

.buttonsSpecials:hover {
  transform: scale(.98);
  transition: transform .2s;
}

/* ---------------------------------
div Special > svg
 --------------------------------- */

.divSpecial {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  align-items: center;

  overflow-y: hidden;

  gap: 20px;
  padding: 2px 10px;

  &>a {
    width: 80px;
    height: 80px;

    min-width: 80px;
    min-height: 80px;

    cursor: alias;

    &>svg {
      width: 80px;
      height: 80px;
    }
  }
}


/* ---------------------------------
Botones especiales EXTENDIDAS
 --------------------------------- */

.btnSpecialOpen {
  margin-top: 0px !important;

  &>.divSpecial {
    height: auto;
  }
}

.footer {
  margin-top: 1.2em;
  min-height: 40px;
  width: 100%;
  background-color: brown;
}