html {
  background-color: #282c34;
}
ul,li, button{
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
}

.App {
  position: absolute;
  display: grid;
  grid-template-rows: 40px auto;
  text-align: center;

  width: 100%;
  height: 100%;

  overflow: hidden;
}

.header {
  display: grid;
  width: 100%;

  justify-items: center;

  &>header {
    margin-top: 10px;
    width: 90%;
    height: 100%;
    outline: 2px solid rgb(12, 138, 149);
  }
}